import nextId from 'uuid/v4';
export var actions = {
    addTodo: function (label) { return ({ type: 'addTodo', id: nextId(), label: label }); },
    remove: function (id) { return ({ type: 'remove', id: id }); },
    complete: function (id) { return ({ type: 'complete', id: id }); },
    clear: function () { return ({ type: 'clear' }); },
    setFilter: function (filter) { return ({ type: 'setFilter', filter: filter }); }
};
